<template>
    <div class="CMwrap">
      <div class="manage">
          <div tag="div"  @click="toSafe" class="listdetail">
            <span class="info">修改手机号</span>
            <div class="phone">
              {{this.secphone}} <span class="iconfont icon-yousanjiao1"></span>
            </div>
          </div>
        <router-link tag="div" :to="{ name:'safe',params:{ typeId:2 }}" class="listdetail">
          <span class="info">设置登录密码</span>
          <div class="phone">
             <span class="iconfont icon-yousanjiao1"></span>
          </div>
        </router-link>
        <router-link tag="div" :to="{ name:'safe',params:{ typeId:3 }}" class="listdetail">
          <span class="info">设置兑换密码</span>
          <div class="phone">
             <span class="iconfont icon-yousanjiao1"></span>
          </div>
        </router-link>
      </div>
    </div>
</template>

<script>
  import { setStore } from '../assets/utils';
    export default {
        name: "countManege",
      data(){
          return{
           phone:'',
          }
      },
      created(){
        setStore('activeNum',0)
      },
      mounted(){
        this.phone=this.$cookie.get('cellphone');
        console.log(this.phone)
      },
      computed:{
        secphone: function () {
          return this.phone.substring(0, 3) + "****" + this.phone.substring(7);
        },
      },
      methods:{
        toSafe(){
          setStore('activeNum',1);
          this.$router.push({
            name:'safe',
            params:{ typeId:1 }
          })
        }
      }
    }
</script>

<style scoped lang="scss">
  .CMwrap{
    height: 100%;
    padding-top: 0.18rem;
    background: #E6E9ED;
    position: relative;
    .manage{
      width: 3.5rem;
      height: 1.52rem;
      background: white;
      border-radius: 0.04rem;
      margin: 0 auto;
    }
    .listdetail{
      display: flex;
      padding: 0.13rem;
      justify-content: space-between;
      border-bottom: 1px solid #eee;
      color: #999;
      font-size: 0.14rem;
      .info{
        color: #333;
        font-size: 0.17rem;
      }
    }

  }

</style>



